
import { ISerializable } from './common'

export interface ActionEditData {
    id: number
    name: string
    parent_action_id: number
    child_action_ids: number[]
}

export class Action implements ISerializable {

    public id: number = 0
    public name: string = ''

    public parent_action_id: number = 0
    public child_action_ids: number[] = []
    
    constructor() {
        
    }

    // DO NOT call this method directly.
    public serialize(): ActionEditData {
        return {
            id: this.id,
            name: this.name,
            parent_action_id: this.parent_action_id,
            child_action_ids: this.child_action_ids.slice(),
        }
    }

    // DO NOT call this method directly.
    public deserialize(data: ActionEditData): void {
        this.id = data.id
        this.name = data.name
        this.parent_action_id = data.parent_action_id
        this.child_action_ids = data.child_action_ids.slice()
    }
}



