
export class Event<T> {

    private handlers: ((T) => void)[] = []
    
    constructor() {
        
    }

    public add_listener(handler: (T) => void): void {
        this.handlers.push(handler)
    }

    public dispatch(data: T): void {
        for (let handler of this.handlers) {
            handler(data)
        }
    }
    
}



