
import { Injectable } from '@angular/core'

import { Global } from './models'

import {
    Util,
    IChangable,
    ISerializable,
    Event,
    Action, ActionEditData,
} from '../common/common'


@Injectable()
export class TaskData implements IChangable, ISerializable {

    public changed: Event<any> = new Event<any>()

    private actions: {[id: number]: Action} = {}

    constructor(
        private global: Global
    ) {
        let self = this
        this.initialize()
        this.global.wait_for_translation().then(function () {
            self.set_root_action_name()
        })
    }

    private initialize(): void {
        let action: Action = new Action()
        action.id = 0
        this.actions[0] = action

        // or load data.
    }

    private set_root_action_name(): void {
        this.actions[0].name = this.global.tr.ROOT_ACTION_NAME
    }

    // tested
    public get_first_unused_key(dict: {[id: number]: any}): number {
        let keys = []
        for (let key in dict) {
            keys.push(parseInt(key))
        }
        keys.sort()
        let i = 0
        for (let key of keys) {
            if (key > i + 1) {
                return i + 1
            }
            i = key
        }
        return i + 1
    }

    public get_next_action_id(): number {
        return this.get_first_unused_key(this.actions)
    }

    // public get_next_mission_id(): number {
        
    // }

    // DO NOT modify its id
    public get_new_action_edit(): ActionEditData {
        let action: Action = new Action()
        let action_edit: ActionEditData = action.serialize()
        action_edit.id = this.get_next_action_id()
        
        return action_edit
    }

    public apply_new_action_edit(data: ActionEditData): void {
        let action: Action = new Action()
        action.deserialize(data)
        this.actions[data.id] = action

        // move node
        this.get_action(data.parent_action_id).child_action_ids.push(data.id)

        this.changed.dispatch(null)
    }

    public action_exists(id: number): boolean {
        return id in this.actions
    }

    public get_action(id: number): Action {
        return this.actions[id]
    }

    public delete_action(id: number): void {
        delete this.actions[id]

        this.changed.dispatch(null)
    }

    public get_action_edit(id: number): ActionEditData {
        return this.get_action(id).serialize()
    }

    public apply_action_edit(id: number, data: ActionEditData): void {
        let action: Action = this.get_action(id)
        
        // move node
        Util.array_remove(this.get_action(action.parent_action_id).child_action_ids, data.id)
        this.get_action(data.parent_action_id).child_action_ids.push(data.id)
        
        action.deserialize(data)
        
        this.changed.dispatch(null)
    }
    
    public serialize(): any {
        alert('TaskData::serialize not implemented')
        let s_actions = {}
        for (let key in this.actions) {
            s_actions[key] = this.actions[key].serialize()
        }
        return {
            actions: s_actions,
        }
    }

    public deserialize(data: any): void {
        alert('TaskData::deserialize not implemented')
        this.actions = {}
        for (let key in data.actions) {
            let action = new Action()
            action.deserialize(data.actions[key])
            this.actions[key] = action
        }
    }
    
}



